@import "normalizes.scss";
@import "resets.scss";

* {
   box-sizing: border-box; }

@font-face {
   font-family: "MontserratT";
   src: url('../fonts/Montserrat-Regular.ttf');
   font-weight: regular;
   font-style: normal; }

@font-face {
   font-family: "MontserratT";
   src: url('../fonts/Montserrat-SemiBold.ttf');
   font-weight: bold;
   font-style: normal; }

html {
   font-family: "MontserratT", sans-serif;
   font-weight: regular; }

$color-blue: #5396F6;
$color-orange: #F68A66;

.container {
   max-width: 1140px;
   margin: 0 auto;
   position: relative; }

// Main
.main-section {
   min-height: 800px;
   background: #f0f0f0;
   padding-top: 20px;
   position: relative;
   overflow: hidden;
   &__nav-container {
      display: flex;
      justify-content: space-between;
      align-items: center; }
   &__logo {
      width: 130px; }
   &__content-container {
      display: flex;
      flex-direction: row;
      position: relative; }
   &__first-elipse {
      position: absolute;
      top: 85px;
      right: -28px;
      z-index: 1; }
   &__second-elipse {
      position: absolute;
      right: -120px;
      top: 630px; } }

.nav-menu {
   &__list {
      display: flex;
      flex-direction: row; }
   &__list-item {
      margin-right: 80px;
      font-size: 18px;
      font-weight: bold;
      &:last-child {
         margin-right: 0; }
      a {
         text-decoration: none;
         color: #000; } } }

// Main left
.left-wrapper {
   position: relative;
   img {
      z-index: 4;
      position: relative; }
   &__rectangle-blue {
      position: absolute;
      z-index: 2;
      top: 150px;
      left: -50px;
      width: 630px;
      height: 220px;
      background: $color-blue;
      border-radius: 100px;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      transform: rotate(-63.04deg); }
   &__rectangle-orange {
      position: absolute;
      z-index: 3;
      width: 632px;
      height: 220px;
      left: 0px;
      top: 200px;
      background: $color-orange;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      border-radius: 100px;
      transform: rotate(-45deg); }
   &__rectangle-grey {
      position: absolute;
      z-index: 1;
      width: 630px;
      height: 220px;
      left: 50px;
      top: 250px;
      background: #C1C1C1;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      border-radius: 100px;
      transform: rotate(-25.34deg); } }

// Main right
.right-wrapper {
   width: 350px;
   position: absolute;
   top: 80px;
   right: 20px;
   z-index: 2;
   &__icons-box {
      margin-bottom: 10px; }
   &__title {
      margin-bottom: 10px; }
   &__text {
      font-size: 16px;
      letter-spacing: 0.03em;
      margin-bottom: 30px; } }
.icons {
   display: flex;
   flex-direction: row;
   &__item {
      border: 2px solid $color-blue;
      border-radius: 100px;
      width: 42px;
      height: 42px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 20px; }
   &__item:last-child {
      margin-right: 0;
      i {
         font-size: 20px; } } }

.title {
   font-size: 65px;
   position: relative;
   font-weight: bold;
   &__hr {
      width: 83px;
      position: absolute;
      left: 3px;
      bottom: 0;
      margin: 0;
      border: none;
      border-top: 1.5px solid #000; } }

.buttons {
   display: inline-flex;
   &__main-btn {
      margin-right: 30px;
      width: 200px;
      height: 60px;
      border: none;
      cursor: pointer;
      border-radius: 100px;
      background: $color-orange;
      filter: drop-shadow(2px 5px 8px rgba(0, 0, 0, 0.25));
      font-size: 16px;
      color: #fff;
      font-weight: bold; }
   &__icon-btn {
      display: inline-flex;
      align-items: center;
      img {
         margin-right: 15px; }
      &__text {
         font-size: 16px; } } }

// Elipse
.elipse {
   width: 195px;
   height: 195px;
   background: linear-gradient(135deg, rgba(239, 113, 65, 0.5) 15.18%, rgba(79, 146, 241, 0.5) 50.61%, rgba(44, 44, 44, 0.5) 86.04%);
   border-radius: 150px;
   display: flex;
   justify-content: center;
   align-items: center;
   &_big {
      width: 280px;
      height: 280px;
      transform: rotate(52deg); }
   &__subtract {
      width: 123px;
      height: 123px;
      background: #f0f0f0;
      border-radius: 150px;
      &_big {
         width: 180px;
         height: 180px; }
      &_white {
         background: #fff; } } }

// Second section
.second-section {
   min-height: 700px;
   &__container {
      padding-top: 35px;
      position: relative; }
   &__title {
      margin-bottom: 30px; }
   &__cards-box {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 35px; }
   &__icons-box {
      display: flex;
      justify-content: space-evenly; }
   &__first-elipse {
      position: absolute;
      top: 65px;
      right: -65px;
      z-index: -1; }
   &__second-elipse {
      position: absolute;
      top: 364px;
      left: -80px;
      z-index: -1; } }

.cards-item {
   position: relative;
   width: 360px;
   height: 360px;
   background: #E6E6E6;
   border-radius: 20px;
   display: flex;
   align-items: flex-end;
   filter: drop-shadow(4px 5px 5px rgba(0, 0, 0, 0.25));
   &__img {
      height: 345px; }
   &__text {
      width: 280px;
      font-size: 36px;
      text-align: center;
      transform: rotate(90deg);
      position: absolute;
      top: 140px;
      right: -35px;
      font-weight: bold; } }

.cards-item_orange {
   background: $color-orange; }
.cards-item_blue {
   background: $color-blue; }

// second section icons
.icons-item {
   display: flex;
   align-items: center;
   &__img {
      width: 80px;
      margin-right: 20px; }
   &__text {
      font-size: 18px;
      span {
         font-size: 44px;
         font-weight: bold; } } }

// Third section
.third-section {
   min-height: 640px;
   background: #F0F0F0;
   position: relative;
   overflow: hidden;
   &__container {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding-left: 160px;
      justify-content: space-between;
      padding-top: 40px;
      position: relative; }
   &__img-1 {
      position: absolute;
      top: 160px;
      left: 97px;
      opacity: 0.8; }
   &__img-2 {
      position: absolute;
      top: 240px;
      left: 20px;
      opacity: 0.8; }
   &__right-wrapper {
      max-width: 480px; }
   &__right-title {
      margin-bottom: 20px; }
   &__text {
      font-size: 18px;
      margin-bottom: 30px; }
   &__second-elipse {
      position: absolute;
      top: -110px;
      right: -110px;
      transform: rotate(-42deg); } }

.title__hr_top {
   bottom: 65px; }

// Footer
.footer {
   &__img {
      position: absolute;
      left: -150px;
      bottom: 0px; }
   &__container {
      min-height: 275px;
      display: flex;
      align-items: center;
      justify-content: flex-end; }
   &__title {
      margin-bottom: 25px; } }


